.footer__container {
	background-color: #242424;
	padding: 4rem 0 2rem 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.footer__subscription {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;

	margin-bottom: 24px;
	padding: 24px;
	color: #fff;
}

.footer__subscription>p {
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
}

.footer__subscription__heading {
	margin-bottom: 24px;
	font-size: 24px;
}

.footer__subscription__text {
	margin-bottom: 24px;
	font-size: 20px;
}

.footer__input {
	padding: 8px 20px;
	border-radius: 2px;
	margin-right: 10px;
	outline: none;
	border: none;
	font-size: 18px;
	margin-bottom: 16px;
	border: 1px solid #fff;
}

.footer__links {
	width: 100%;
	max-width: 1000px;
	display: flex;
	justify-content: center;
}

.footer__link__wrapper {
	display: flex;
}

.footer__link__items {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 16px;
	text-align: left;
	width: 160px;
	box-sizing: border-box;
}

.footer__link__items h2 {
	margin-bottom: 16px;
}

.footer__link__items>h2 {
	color: #fff;
}

.footer__link__items a {
	color: #fff;
	text-decoration: none;
	margin-bottom: 0.5rem;
}

.footer__link__items a:hover {
	color: #e9e9e9;
	transition: 0.3s ease-out;
}

.footer-email-form h2 {
	margin-bottom: 2rem;
}

.footer-input::placeholder {
	color: #b1b1b1;
}

.social__media {
	max-width: 1000px;
	width: 100%;
}

.social__media__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 90%;
	max-width: 1000px;
	margin: 40px auto 0 auto;
}

@media screen and (max-width: 820px) {
	.footer__links {
		padding-top: 2rem;
	}

	.footer__input {
		width: 100%;
	}

	.btn {
		width: 100%;
	}

	.footer__link__wrapper {
		flex-direction: column;
	}

	.social__media__wrap {
		flex-direction: column;
	}
}

@media screen and (max-width: 768px) {}
